import React from 'react';
import useSoundcloud from 'src/hook/use-soundcloud';
import styles from './style.module.scss';
import PlayArrowRoundedIcon from '@material-ui/icons/PlayArrowRounded';
import PauseRoundedIcon from '@material-ui/icons/PauseRounded';
import SkipNextRoundedIcon from '@material-ui/icons/SkipNextRounded';
import SkipPreviousRoundedIcon from '@material-ui/icons/SkipPreviousRounded';
import StopRoundedIcon from '@material-ui/icons/StopRounded';

const AudioPlayer = () => {
  const { currentTrack, loaded, play, stop, pause, nextTrack, previousTrack } = useSoundcloud();

  console.log('AudioPlayer', loaded, currentTrack);

  return (
    <div className={styles.container}>
      <div className={styles.content}>
        {loaded ? <img alt={currentTrack.title} className={styles.artwork} src={currentTrack.artwork_url} /> : null}
        {loaded ? (
          <div>
            <h4 className={styles.title}>{currentTrack.title.toUpperCase()}</h4>
            <div className={styles.info}>
              <span className={styles.time}>
                IIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIIII
              </span>
              <span className={styles.time}>00 : 24</span>
            </div>
          </div>
        ) : null}
      </div>
      <div className={styles.controls}>
        <PlayArrowRoundedIcon htmlColor={'#e8e8e8'} onClick={play} style={{ height: 20, width: 20 }} />
        <StopRoundedIcon htmlColor={'#e8e8e8'} onClick={stop} style={{ height: 20, width: 20 }} />
        <PauseRoundedIcon htmlColor={'#e8e8e8'} onClick={pause} style={{ height: 20, width: 20 }} />
        <SkipPreviousRoundedIcon htmlColor={'#e8e8e8'} onClick={previousTrack} style={{ height: 20, width: 20 }} />
        <SkipNextRoundedIcon htmlColor={'#e8e8e8'} onClick={nextTrack} style={{ height: 20, width: 20 }} />
      </div>
    </div>
  );
};

export default React.memo(AudioPlayer);
