import SoundcloudAudio from 'soundcloud-audio';
import { useState, useEffect } from 'react';
import { Track } from 'src/type/music';

const scPlayer = new SoundcloudAudio('6CDFwX2Aif1DFb9g6oq5JB8j3BKtwPPF');
const url = 'https://soundcloud.com/twisttted/sets/aro-io';

const useSoundcloud = () => {
  const [tracks, setTracks] = useState<Track[]>([]);
  const [loaded, setLoaded] = useState(false);
  const [currentIndex, setCurrentIndex] = useState(0);

  const handleOnLoaded = (set: any) => {
    const { tracks } = set;

    setTracks(tracks);
    setLoaded(true);

    console.log(set);

    // scPlayer.play();

    scPlayer.on('ended', function() {
      scPlayer.next();
    });

    scPlayer.on('timeupdate', function() {
      console.log(scPlayer.audio.currentTime);
    });
  };

  useEffect(() => {
    scPlayer.resolve(url, handleOnLoaded);
  }, []);

  const nextTrack = () => {
    const index = currentIndex < tracks.length ? currentIndex + 1 : 0;

    setCurrentIndex(index);
    scPlayer.next();
  };

  const previousTrack = () => {
    const index = currentIndex > 0 ? currentIndex - 1 : 0;

    setCurrentIndex(index);
    scPlayer.previous();
  };

  const play = () => {
    scPlayer.play();
  };

  const stop = () => {
    scPlayer.stop();
  };

  const pause = () => {
    scPlayer.stop();
  };

  console.log('use sound Cloud');

  return {
    currentTrack: tracks[currentIndex],
    loaded,
    nextTrack,
    pause,
    play,
    previousTrack,
    stop,
  };
};

export default useSoundcloud;
