import Phaser from 'phaser';
import BreakoutScene from './scene';

const GAME_HEIGHT = 480;
const GAME_WIDTH = 320;

const config = {
  type: Phaser.WEBGL,
  width: GAME_WIDTH,
  height: GAME_HEIGHT,
  scene: [BreakoutScene],
  physics: {
    default: 'arcade',
  },
};

const breakoutGame = (el: string) => {
  return new Phaser.Game({ ...config, parent: el });
};

export default breakoutGame;
